'use client'

import { useState } from 'react'

interface User {
  id: string
  username: string
  room: string
  joinedAt: string
}

interface UserListProps {
  users: User[]
  currentUser: string
  onSendPrivateMessage: (user: User, message: string) => void
}

export default function UserList({ users, currentUser, onSendPrivateMessage }: UserListProps) {
  const [selectedUser, setSelectedUser] = useState<User | null>(null)
  const [privateMessage, setPrivateMessage] = useState('')
  const [showPrivateInput, setShowPrivateInput] = useState(false)

  const handleSendPrivateMessage = () => {
    if (selectedUser && privateMessage.trim()) {
      onSendPrivateMessage(selectedUser, privateMessage.trim())
      setPrivateMessage('')
      setShowPrivateInput(false)
      setSelectedUser(null)
    }
  }

  const handleUserClick = (user: User) => {
    if (user.username === currentUser) return
    
    setSelectedUser(user)
    setShowPrivateInput(true)
  }

  const formatJoinTime = (timestamp: string) => {
    const date = new Date(timestamp)
    const now = new Date()
    const diffInMinutes = Math.floor((now.getTime() - date.getTime()) / (1000 * 60))
    
    if (diffInMinutes < 1) return 'Just joined'
    if (diffInMinutes < 60) return `${diffInMinutes}m ago`
    if (diffInMinutes < 1440) return `${Math.floor(diffInMinutes / 60)}h ago`
    return date.toLocaleDateString()
  }

  return (
    <div className="h-full flex flex-col">
      {/* Header */}
      <div className="p-4 border-b border-gray-200">
        <h3 className="font-semibold text-gray-900">Online Users</h3>
        <p className="text-sm text-gray-500">{users.length} user{users.length !== 1 ? 's' : ''}</p>
      </div>

      {/* User List */}
      <div className="flex-1 overflow-y-auto">
        {users.length === 0 ? (
          <div className="p-4 text-center text-gray-500">
            <p>No users online</p>
          </div>
        ) : (
          <div className="p-2 space-y-1">
            {users.map((user) => (
              <div
                key={user.id}
                onClick={() => handleUserClick(user)}
                className={`p-3 rounded-lg transition-colors cursor-pointer ${
                  user.username === currentUser
                    ? 'bg-blue-50 border border-blue-200'
                    : 'hover:bg-gray-50'
                }`}
              >
                <div className="flex items-center justify-between">
                  <div className="flex items-center space-x-2">
                    <div className="w-2 h-2 bg-green-500 rounded-full"></div>
                    <span className={`font-medium ${
                      user.username === currentUser ? 'text-blue-700' : 'text-gray-900'
                    }`}>
                      {user.username}
                      {user.username === currentUser && ' (You)'}
                    </span>
                  </div>
                </div>
                <p className="text-xs text-gray-500 mt-1">
                  {formatJoinTime(user.joinedAt)}
                </p>
              </div>
            ))}
          </div>
        )}
      </div>

      {/* Private Message Input */}
      {showPrivateInput && selectedUser && (
        <div className="border-t border-gray-200 p-4 bg-gray-50">
          <div className="mb-2">
            <p className="text-sm font-medium text-gray-700">
              Send private message to {selectedUser.username}
            </p>
          </div>
          
          <div className="space-y-2">
            <textarea
              value={privateMessage}
              onChange={(e) => setPrivateMessage(e.target.value)}
              placeholder="Type your private message..."
              className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none resize-none"
              rows={2}
            />
            
            <div className="flex space-x-2">
              <button
                onClick={handleSendPrivateMessage}
                disabled={!privateMessage.trim()}
                className="flex-1 px-3 py-1 bg-blue-600 text-white text-sm rounded-md hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
              >
                Send
              </button>
              <button
                onClick={() => {
                  setShowPrivateInput(false)
                  setSelectedUser(null)
                  setPrivateMessage('')
                }}
                className="px-3 py-1 bg-gray-300 text-gray-700 text-sm rounded-md hover:bg-gray-400 transition-colors"
              >
                Cancel
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Instructions */}
      <div className="p-4 border-t border-gray-200 bg-gray-50">
        <p className="text-xs text-gray-500 text-center">
          Click on a user to send a private message
        </p>
      </div>
    </div>
  )
}
