'use client'

import { useState } from 'react'

interface Message {
  id: string
  username: string
  text: string
  timestamp: string
  room: string
  reactions?: { [key: string]: string[] }
  readBy?: string[]
  type?: 'file' | 'image' | 'text'
  fileUrl?: string
  fileName?: string
}

interface MessageItemProps {
  message: Message
  isOwnMessage: boolean
  onReaction: (messageId: string, reaction: string) => void
}

const REACTIONS = ['👍', '❤️', '😂', '😮', '😢', '😡']

export default function MessageItem({ message, isOwnMessage, onReaction }: MessageItemProps) {
  const [showReactions, setShowReactions] = useState(false)

  const formatTime = (timestamp: string) => {
    return new Date(timestamp).toLocaleTimeString([], { 
      hour: '2-digit', 
      minute: '2-digit' 
    })
  }

  const handleReactionClick = (reaction: string) => {
    onReaction(message.id, reaction)
    setShowReactions(false)
  }

  const getTotalReactions = () => {
    if (!message.reactions) return 0
    return Object.values(message.reactions).reduce((total, users) => total + users.length, 0)
  }

  const renderFileContent = () => {
    if (message.type === 'file' && message.fileUrl) {
      return (
        <div className="mt-2 p-3 bg-gray-50 rounded-lg border">
          <div className="flex items-center space-x-2">
            <span className="text-2xl">📎</span>
            <div>
              <p className="font-medium text-sm">{message.fileName}</p>
              <a 
                href={message.fileUrl} 
                target="_blank" 
                rel="noopener noreferrer"
                className="text-blue-600 hover:text-blue-800 text-sm underline"
              >
                Download
              </a>
            </div>
          </div>
        </div>
      )
    }

    if (message.type === 'image' && message.fileUrl) {
      return (
        <div className="mt-2">
          <img 
            src={message.fileUrl} 
            alt={message.fileName || 'Shared image'}
            className="max-w-xs max-h-64 rounded-lg border"
            loading="lazy"
          />
        </div>
      )
    }

    return null
  }

  return (
    <div className={`flex ${isOwnMessage ? 'justify-end' : 'justify-start'}`}>
      <div className={`max-w-xs lg:max-w-md ${isOwnMessage ? 'order-2' : 'order-1'}`}>
        <div
          className={`relative group px-4 py-2 rounded-2xl ${
            isOwnMessage
              ? 'bg-blue-600 text-white'
              : 'bg-white text-gray-900 border border-gray-200'
          }`}
        >
          {/* Username (only show for others' messages) */}
          {!isOwnMessage && (
            <p className="text-xs font-medium text-gray-600 mb-1">
              {message.username}
            </p>
          )}

          {/* Message Text */}
          <p className="text-sm whitespace-pre-wrap break-words">
            {message.text}
          </p>

          {/* File Content */}
          {renderFileContent()}

          {/* Timestamp */}
          <p className={`text-xs mt-1 ${
            isOwnMessage ? 'text-blue-100' : 'text-gray-500'
          }`}>
            {formatTime(message.timestamp)}
          </p>

          {/* Reaction Button */}
          <button
            onClick={() => setShowReactions(!showReactions)}
            className={`absolute -bottom-2 ${isOwnMessage ? 'left-2' : 'right-2'} 
              opacity-0 group-hover:opacity-100 transition-opacity
              w-6 h-6 rounded-full bg-gray-200 hover:bg-gray-300 
              flex items-center justify-center text-xs`}
          >
            +
          </button>

          {/* Reaction Picker */}
          {showReactions && (
            <div className={`absolute top-full mt-2 ${isOwnMessage ? 'right-0' : 'left-0'} 
              bg-white border border-gray-200 rounded-lg shadow-lg p-2 flex space-x-1 z-10`}>
              {REACTIONS.map((reaction) => (
                <button
                  key={reaction}
                  onClick={() => handleReactionClick(reaction)}
                  className="w-8 h-8 rounded hover:bg-gray-100 flex items-center justify-center text-lg"
                >
                  {reaction}
                </button>
              ))}
            </div>
          )}
        </div>

        {/* Reactions Display */}
        {message.reactions && getTotalReactions() > 0 && (
          <div className={`flex flex-wrap gap-1 mt-1 ${isOwnMessage ? 'justify-end' : 'justify-start'}`}>
            {Object.entries(message.reactions).map(([reaction, users]) => 
              users.length > 0 && (
                <span
                  key={reaction}
                  className="inline-flex items-center px-2 py-1 rounded-full bg-gray-100 text-xs"
                  title={`${users.join(', ')} reacted with ${reaction}`}
                >
                  {reaction} {users.length}
                </span>
              )
            )}
          </div>
        )}

        {/* Read Receipt */}
        {isOwnMessage && message.readBy && message.readBy.length > 1 && (
          <p className="text-xs text-gray-500 mt-1 text-right">
            Read by {message.readBy.length - 1} user{message.readBy.length - 1 !== 1 ? 's' : ''}
          </p>
        )}
      </div>
    </div>
  )
}
