'use client'

import { useEffect, useState, useRef } from 'react'
import { io, Socket } from 'socket.io-client'

const SOCKET_SERVER_URL = process.env.NEXT_PUBLIC_SOCKET_SERVER_URL || 'http://localhost:4000'

export const useSocket = () => {
  const [socket, setSocket] = useState<Socket | null>(null)
  const [isConnected, setIsConnected] = useState(false)
  const [connectionError, setConnectionError] = useState<string | null>(null)
  const reconnectAttempts = useRef(0)
  const maxReconnectAttempts = 5

  useEffect(() => {
    const socketInstance = io(SOCKET_SERVER_URL, {
      reconnectionAttempts: maxReconnectAttempts,
      reconnectionDelay: 1000,
      reconnectionDelayMax: 5000,
      timeout: 20000,
    })

    // Connection event handlers
    socketInstance.on('connect', () => {
      console.log('Connected to server:', socketInstance.id)
      setIsConnected(true)
      setConnectionError(null)
      reconnectAttempts.current = 0
    })

    socketInstance.on('disconnect', (reason: string) => {
      console.log('Disconnected from server:', reason)
      setIsConnected(false)
      
      if (reason === 'io server disconnect') {
        // Server disconnected the socket, need to reconnect manually
        socketInstance.connect()
      }
    })

    socketInstance.on('connect_error', (error: Error) => {
      console.error('Connection error:', error)
      reconnectAttempts.current++
      
      if (reconnectAttempts.current >= maxReconnectAttempts) {
        setConnectionError('Failed to connect to server. Please check your connection and try again.')
      } else {
        setConnectionError(`Connection attempt ${reconnectAttempts.current}/${maxReconnectAttempts}...`)
      }
    })

    socketInstance.on('reconnect', (attemptNumber: number) => {
      console.log('Reconnected after', attemptNumber, 'attempts')
      setIsConnected(true)
      setConnectionError(null)
    })

    socketInstance.on('reconnect_error', (error: Error) => {
      console.error('Reconnection error:', error)
    })

    socketInstance.on('reconnect_failed', () => {
      console.error('Failed to reconnect after maximum attempts')
      setConnectionError('Unable to reconnect to server. Please refresh the page.')
    })

    setSocket(socketInstance)

    // Cleanup on unmount
    return () => {
      socketInstance.disconnect()
    }
  }, [])

  const reconnect = () => {
    if (socket) {
      reconnectAttempts.current = 0
      setConnectionError(null)
      socket.connect()
    }
  }

  return {
    socket,
    isConnected,
    connectionError,
    reconnect
  }
}

export default useSocket
