'use client'

import { useEffect, useState, useRef } from 'react'
import { Socket } from 'socket.io-client'
import MessageList from './MessageList'
import ChatInput from './ChatInput'
import UserList from './UserList'

interface Message {
  id: string
  username: string
  text: string
  timestamp: string
  room: string
  reactions?: { [key: string]: string[] }
  readBy?: string[]
  type?: 'file' | 'image' | 'text'
  fileUrl?: string
  fileName?: string
}

interface PrivateMessage {
  id: string
  from: string
  fromId: string
  to: string
  toId: string
  text: string
  timestamp: string
  type: 'private'
}

interface User {
  id: string
  username: string
  room: string
  joinedAt: string
}

interface ChatWindowProps {
  username: string
  socket: Socket
  onLeaveChat: () => void
}

export default function ChatWindow({ username, socket, onLeaveChat }: ChatWindowProps) {
  const [messages, setMessages] = useState<Message[]>([])
  const [privateMessages, setPrivateMessages] = useState<PrivateMessage[]>([])
  const [onlineUsers, setOnlineUsers] = useState<User[]>([])
  const [currentRoom, setCurrentRoom] = useState('global')
  const [currentRoomName, setCurrentRoomName] = useState('Global Chat')
  const [typingUsers, setTypingUsers] = useState<string[]>([])
  const [notifications, setNotifications] = useState<string[]>([])
  const [unreadCount, setUnreadCount] = useState(0)
  const [showUserList, setShowUserList] = useState(false)
  const [isWindowFocused, setIsWindowFocused] = useState(true)
  const messagesEndRef = useRef<HTMLDivElement>(null)

  // Auto-scroll to bottom when new messages arrive
  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' })
  }

  useEffect(() => {
    scrollToBottom()
  }, [messages])

  // Handle window focus for unread count
  useEffect(() => {
    const handleFocus = () => {
      setIsWindowFocused(true)
      setUnreadCount(0)
    }
    const handleBlur = () => setIsWindowFocused(false)

    window.addEventListener('focus', handleFocus)
    window.addEventListener('blur', handleBlur)

    return () => {
      window.removeEventListener('focus', handleFocus)
      window.removeEventListener('blur', handleBlur)
    }
  }, [])

  // Socket event listeners
  useEffect(() => {
    if (!socket) return

    // Handle incoming messages
    socket.on('message', (message: Message) => {
      setMessages(prev => [...prev, message])
      
      // Show notification if window is not focused
      if (!isWindowFocused && message.username !== username) {
        setUnreadCount(prev => prev + 1)
        
        // Browser notification
        if (Notification.permission === 'granted') {
          new Notification(`New message from ${message.username}`, {
            body: message.text,
            icon: '/favicon.ico'
          })
        }
      }
    })

    // Handle previous messages when joining
    socket.on('previous-messages', (messages: Message[]) => {
      setMessages(messages)
    })

    // Handle user joined
    socket.on('user-joined', (data: { user: User; message: string; onlineUsers: User[] }) => {
      setOnlineUsers(data.onlineUsers)
      setNotifications(prev => [...prev, data.message])
      setTimeout(() => {
        setNotifications(prev => prev.slice(1))
      }, 3000)
    })

    // Handle user left
    socket.on('user-left', (data: { username: string; message: string; onlineUsers: User[] }) => {
      setOnlineUsers(data.onlineUsers)
      setNotifications(prev => [...prev, data.message])
      setTimeout(() => {
        setNotifications(prev => prev.slice(1))
      }, 3000)
    })

    // Handle online users
    socket.on('online-users', (users: User[]) => {
      setOnlineUsers(users)
    })

    // Handle typing indicators
    socket.on('typing', (data: { username: string; isTyping: boolean }) => {
      if (data.isTyping) {
        setTypingUsers(prev => [...prev.filter(u => u !== data.username), data.username])
      } else {
        setTypingUsers(prev => prev.filter(u => u !== data.username))
      }
    })

    // Handle private messages
    socket.on('private-message', (message: PrivateMessage) => {
      setPrivateMessages(prev => [...prev, message])
      
      if (!isWindowFocused && message.from !== username) {
        setUnreadCount(prev => prev + 1)
        
        if (Notification.permission === 'granted') {
          new Notification(`Private message from ${message.from}`, {
            body: message.text,
            icon: '/favicon.ico'
          })
        }
      }
    })

    // Handle message reactions
    socket.on('message-reaction', (data: { messageId: string; reactions: { [key: string]: string[] } }) => {
      setMessages(prev => prev.map(msg => 
        msg.id === data.messageId 
          ? { ...msg, reactions: data.reactions }
          : msg
      ))
    })

    return () => {
      socket.off('message')
      socket.off('previous-messages')
      socket.off('user-joined')
      socket.off('user-left')
      socket.off('online-users')
      socket.off('typing')
      socket.off('private-message')
      socket.off('message-reaction')
    }
  }, [socket, username, isWindowFocused])

  // Request notification permission
  useEffect(() => {
    if (Notification.permission === 'default') {
      Notification.requestPermission()
    }
  }, [])

  const handleReaction = (messageId: string, reaction: string) => {
    socket?.emit('react-message', { messageId, reaction, room: currentRoom })
  }

  const handleSendPrivateMessage = (targetUser: User, message: string) => {
    socket?.emit('private-message', {
      to: targetUser.username,
      toId: targetUser.id,
      text: message
    })
  }

  return (
    <div className="flex h-screen bg-gray-100">
      {/* Main Chat Area */}
      <div className="flex-1 flex flex-col">
        {/* Header */}
        <header className="bg-white border-b border-gray-200 px-4 py-3 flex items-center justify-between">
          <div className="flex items-center space-x-4">
            <h1 className="text-xl font-semibold text-gray-900">{currentRoomName}</h1>
            <span className="text-sm text-gray-500">
              {onlineUsers.length} user{onlineUsers.length !== 1 ? 's' : ''} online
            </span>
          </div>
          
          <div className="flex items-center space-x-2">
            <button
              onClick={() => setShowUserList(!showUserList)}
              className="px-3 py-1 text-sm bg-blue-100 text-blue-700 rounded-md hover:bg-blue-200 transition-colors"
            >
              Users ({onlineUsers.length})
            </button>
            <button
              onClick={onLeaveChat}
              className="px-3 py-1 text-sm bg-red-100 text-red-700 rounded-md hover:bg-red-200 transition-colors"
            >
              Leave Chat
            </button>
          </div>
        </header>

        {/* Notifications */}
        {notifications.length > 0 && (
          <div className="bg-blue-50 border-b border-blue-200 px-4 py-2">
            {notifications.map((notification, index) => (
              <p key={index} className="text-sm text-blue-700">
                {notification}
              </p>
            ))}
          </div>
        )}

        {/* Messages Area */}
        <div className="flex-1 overflow-y-auto p-4 space-y-4">
          <MessageList 
            messages={messages} 
            currentUser={username}
            onReaction={handleReaction}
          />
          
          {/* Typing Indicators */}
          {typingUsers.length > 0 && (
            <div className="flex items-center space-x-2 text-sm text-gray-500 italic">
              <div className="flex space-x-1">
                <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce"></div>
                <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: '0.1s' }}></div>
                <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: '0.2s' }}></div>
              </div>
              <span>
                {typingUsers.length === 1 
                  ? `${typingUsers[0]} is typing...`
                  : `${typingUsers.slice(0, -1).join(', ')} and ${typingUsers[typingUsers.length - 1]} are typing...`
                }
              </span>
            </div>
          )}
          
          <div ref={messagesEndRef} />
        </div>

        {/* Chat Input */}
        <div className="border-t border-gray-200 bg-white p-4">
          <ChatInput socket={socket} currentRoom={currentRoom} />
        </div>
      </div>

      {/* User List Sidebar */}
      {showUserList && (
        <div className="w-64 bg-white border-l border-gray-200">
          <UserList 
            users={onlineUsers}
            currentUser={username}
            onSendPrivateMessage={handleSendPrivateMessage}
          />
        </div>
      )}

      {/* Unread Count Badge */}
      {unreadCount > 0 && (
        <div className="fixed top-4 right-4 bg-red-500 text-white rounded-full w-8 h-8 flex items-center justify-center text-sm font-bold">
          {unreadCount > 99 ? '99+' : unreadCount}
        </div>
      )}
    </div>
  )
}
