'use client'

import { useEffect, useState } from 'react'
import { useRouter } from 'next/navigation'
import useSocket from '@/hooks/useSocket'
import ChatWindow from '@/components/Chat/ChatWindow'
import ConnectionStatus from '@/components/Chat/ConnectionStatus'

export default function ChatPage() {
  const [username, setUsername] = useState('')
  const [isLoading, setIsLoading] = useState(true)
  const { socket, isConnected, connectionError, reconnect } = useSocket()
  const router = useRouter()

  useEffect(() => {
    // Get username from localStorage
    const storedUsername = localStorage.getItem('chatUsername')
    if (!storedUsername) {
      router.push('/')
      return
    }
    
    setUsername(storedUsername)
    setIsLoading(false)
  }, [router])

  useEffect(() => {
    // Join the chat when socket connects and username is available
    if (socket && isConnected && username) {
      socket.emit('join', { username, room: 'global' })
    }
  }, [socket, isConnected, username])

  const handleLeaveChat = () => {
    localStorage.removeItem('chatUsername')
    if (socket) {
      socket.disconnect()
    }
    router.push('/')
  }

  if (isLoading) {
    return (
      <div className="min-h-screen bg-gray-100 flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto mb-4"></div>
          <p className="text-gray-600">Loading chat...</p>
        </div>
      </div>
    )
  }

  if (connectionError) {
    return (
      <div className="min-h-screen bg-gray-100 flex items-center justify-center p-4">
        <div className="bg-white rounded-lg shadow-lg p-6 max-w-md w-full text-center">
          <div className="text-red-500 text-5xl mb-4">⚠️</div>
          <h2 className="text-xl font-semibold text-gray-900 mb-2">Connection Error</h2>
          <p className="text-gray-600 mb-4">{connectionError}</p>
          <div className="space-y-2">
            <button
              onClick={reconnect}
              className="w-full bg-blue-600 text-white py-2 px-4 rounded-lg hover:bg-blue-700 transition-colors"
            >
              Try Again
            </button>
            <button
              onClick={handleLeaveChat}
              className="w-full bg-gray-300 text-gray-700 py-2 px-4 rounded-lg hover:bg-gray-400 transition-colors"
            >
              Go Back
            </button>
          </div>
        </div>
      </div>
    )
  }

  if (!socket || !isConnected) {
    return (
      <div className="min-h-screen bg-gray-100">
        <ConnectionStatus isConnected={false} />
        <div className="flex items-center justify-center h-full">
          <div className="text-center">
            <div className="animate-pulse text-4xl mb-4">🔄</div>
            <p className="text-gray-600">Connecting to chat server...</p>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-100">
      <ConnectionStatus isConnected={isConnected} />
      <ChatWindow 
        username={username} 
        socket={socket} 
        onLeaveChat={handleLeaveChat}
      />
    </div>
  )
}
